.switch {
  height: 400px;
  background: #CCC;
}
.switch ul {
  display: block;
  width: 100%;
  overflow: hidden;
}
.switch ul li {
  list-style: none;
  height: 100px;
  width: 100px;
  float: left;
  background: #000;
}
.switch ul li:nth-child(2n) {
  background: #ff6600;
}



